*{
    margin:0;
    padding:0;
    text-decoration: none;
    font-family: "Roboto Mono", monospace;
    font-optical-sizing: auto;
   
}
.container{
    display:flex;
   
}

li {
text-decoration: none;
list-style-type: none;
}

.form {
    background: rgb(0 0 63 / 17%);
    padding: 1em;
    border-radius: 1em;
    font-size: 2em;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr)) ;
    gap: 0.5rem;
   align-items: center;
   justify-content: center;
}

input {
    height: 1.5em;
    width: 350px;
    padding-left: 0.5em;
    font-size: 0.7em;
    border-radius: 0.5em;
    border: 1px solid rgba(0, 0, 63, 0.37);
    box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.48);
    -webkit-box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.48);
    -moz-box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.48);
}

ol {
    background: rgb(0 0 63 / 17%);
    padding: 1em;
    border-radius: 1em;
}

main{
    width: 100%;
    overflow: hidden;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr)) ;
    gap: 0.5rem;
   align-items: center;
   justify-content: center;
   
   
}

.mainContent {
    color: #00003f;
    margin-top: 5em;
    padding-top: 2em;
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    max-width: 12;
}

.sidebar{
    background: #ececec;
    z-index: 100;
    height: 100px;
    
    transition: all 0.5s;
    width: 100%;
    position: fixed;
    top:0 ;
    display: flex;
    justify-content: center;
    align-items: center;
}
.top_section{
    display: flex;
    flex-direction: row;
    align-items:center;
    padding:20px 15px;
    align-items: center;
    color: #00003f;
    
}
.logo{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 150px;

}
.logoFooter{
    
    display: flex;
    align-items: center;
    justify-content: center;
    

}
.bars{
    display: flex;
    font-size: 25px;
    margin-left: 50px;
}
.link{
    display: flex;
    color: #040542;
    padding: 10px 15px;
    gap: 15px;
    transition: all 0.5s;
}
.link:hover{
    background:lightskyblue;
    color: #000;
    transition: all 0.5s;
}
.active{
    background: lightskyblue;
    color: #000;
}
.icon, .link_text{
    font-size: 20px;
}

.thcLogo {
    height: 100%;
    width: 100%;
   


}

.heroFront {
    height: 100vh;
    width: 100vw;
    background: #cdcdcd;
    background-image: url(./assets/background2.jpg);
    background-repeat: no-repeat;
    background-position: top;
    background-size: cover;
    position: relative;
    z-index: 1;
}

.overlay {
    position: absolute;
    z-index: 2;
    height: 100%;
    background: rgb(130,43,0);
    background: linear-gradient(357deg, rgba(130,43,0,0.7203256302521008) 0%, rgba(167,4,106,0.6278886554621849) 45%, rgba(0,212,255,0.6643032212885154) 100%);
    width: 100%;
}

.sections1{
    height: 100vh;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr)) ;
    gap: 0.5rem;
    justify-content: center;
    align-items: center;
    justify-items: center;
    background-image: url(./assets/background2.jpg);
    background-repeat: no-repeat;
    background-position: top;
    background-size: cover;
}
.sections{
    height: 70vh;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr)) ;
    gap: 0.5rem;
    justify-content: center;
    align-items: center;
    justify-items: center;
   
}
.heroContent {
    max-width: 1000px;
    display: flex;
    
    justify-content: center;
    position: relative;
    width: 100%;
    z-index: 10;
    color: #FFF;
    font-size: 2.5rem;
    text-shadow: 4px 3px 4px rgba(0, 0, 0, 0.493);
}
.divisorVertical {
    padding: 2em;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr)) ;
    gap: 0.5rem;
    justify-content: center;
    align-items: center;
    justify-items: center;
    width: 100%;
}

.divisorContainers{
    display: flex;
    justify-content: center;
    align-items: center;
}
.aboutUsimage {
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
   
    height: 100%;
    transform: rotateY(180deg);
    border-radius: 100%;
    width: 60%;
}

.rightAboutUs {
    max-width: 80%;
}

@media screen and (max-width: 700px) and (orientation: portrait) {
    .sections {
        height: auto;
    }
}

.footer {
    background: rgb(0 0 63 / 17%);
    padding: 1em;
    height: 10em;
    margin-left: auto;
    margin-right: auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr)) ;
    gap: 0.5rem;
   align-items: center;
   justify-content: center;
   align-content: center;
   
}

.footerConteiner {
    height: 100%;
    max-width: 350px;
}

.formButton {
    background: #00003f;
    height: 2em;
    border-radius: 0.5em;
    color: white;
    font-size: 1em;
    font-weight: 900;
    text-transform:uppercase;
    transition: all ease-in-out 300ms;
    border: none;
}

.formButton:hover {
    background: #d62d2d;
    transform: translateY(-0.1em);
    border: none;
}